//hooks
const Map = () => {
  const [state,setState] = useState({
    accuracy: null,
    altitude: null,
    altitudeAccuracy: null,
    heading: null,
    speed: null,
    timestamp: Date.now(),
    error: false,
  });
  const [stateAlami, setStateAlami] = useState({
    conditionFace : '',
    conditionState : '',
    conditionTxt : 'ìœ„ì¹˜ ì¡°ì • í•´ì£¼ì„¸ìš”',
    conditionBgColor : '#1289A7'
  });
  const [countInCircle,setCountInCircle] = useState(0);
  const [latitude, setLatitude] = useState(37.4882);
  const [longitude, setLongitude] = useState(127.1026);
  const [search,setSearch] = useState('');
  const [formState,setFormState] = useState('none');
  const mounted = true;

  const onEvent = (event: any) => {
    if (mounted) {
      setState({
        ...state,
        accuracy: event.coords.accuracy,
        altitude: event.coords.altitude,
        altitudeAccuracy: event.coords.altitudeAccuracy,
        heading: event.coords.heading,
        speed: event.coords.speed,
        timestamp: event.timestamp,
      });
      setLatitude(event.coords.latitude);
      setLongitude(event.coords.longitude);
    }
  };
  const onError = (error: any) => {
    setState({ ...state, error: error.message });
    console.log('error: ', error);
  };
  let map: any;

  const makeArrayPatient = () => {
    let PatientInfo : Object[] = [];
    if(Patient.mapData) {
      Patient.data.map((value) => {
        let daysGap : number;
        // daysGap = isInFewDays(value.month, value.day);
        const curDay = {
          _month : value.month,
          _date : value.day
        }
        daysGap = DateGapAcumulator(curDay);
        if(daysGap <= 10){
          let sliced =  value.latlng.split(', ');
          let patient = {
            position : value.address,
            lat : parseFloat(sliced[0]),
            lng : parseFloat(sliced[1]),
            month : value.month,
            day : value.day
          }
          PatientInfo = [...PatientInfo, patient];
          if(daysGap <= 1){
            makeMarkerInfected(patient, colorRed);
          } else if (1 < daysGap && daysGap <= 4){
            makeMarkerInfected(patient, colorOrg);
          } else if (4 < daysGap && daysGap <=9){
            makeMarkerInfected(patient, colorGrn);
          } else {
            console.log('MAP none in circle');
          }

          let distance : number;
          const getDistance  = {
            lat : latitude,
            lng : longitude,
            _lat : patient.lat,
            _lng : patient.lng
          }
          distance = PositionDistance(getDistance);
          if(distance < 3600){
            AddCount();
          }
        }
      });
    }
  }

  const AddCount = () => {
    setCountInCircle((prevCount) => prevCount+1);
  }

  const makeMarkerMyPos = (_lat : any,_lng : any) => {
    var markerPosition = new window.kakao.maps.LatLng(
      _lat,
      _lng
    );

    // ë§ˆì»¤ë¥¼ ìƒì„±í•©ë‹ˆë‹¤
    var marker = new window.kakao.maps.Marker({
      position: markerPosition,
    });

    // ë§ˆì»¤ê°€ ì§€ë„ ìœ„ì— í‘œì‹œë˜ë„ë¡ ì„¤ì •í•©ë‹ˆë‹¤
    marker.setMap(map);
    // ì§€ë„ì— í‘œì‹œí•  ì›ì„ ìƒì„±í•©ë‹ˆë‹¤
    var circle = new window.kakao.maps.Circle({
      center: new window.kakao.maps.LatLng(
        _lat,
        _lng
      ), // ì›ì˜ ì¤‘ì‹¬ì¢Œí‘œ ì…ë‹ˆë‹¤
      radius: 2400, // ë¯¸í„° ë‹¨ìœ„ì˜ ì›ì˜ ë°˜ì§€ë¦„ì…ë‹ˆë‹¤ , ëŒ€ìƒí™œë°˜ê²½ 4600 , ì¤‘ìƒí™œë°˜ê²½ 2400
      strokeWeight: 1, // ì„ ì˜ ë‘ê»˜ì…ë‹ˆë‹¤
      strokeColor: '#75B8FA', // ì„ ì˜ ìƒ‰ê¹”ì…ë‹ˆë‹¤
      strokeOpacity: 1, // ì„ ì˜ ë¶ˆíˆ¬ëª…ë„ ì…ë‹ˆë‹¤ 1ì—ì„œ 0 ì‚¬ì´ì˜ ê°’ì´ë©° 0ì— ê°€ê¹Œìš¸ìˆ˜ë¡ íˆ¬ëª…í•©ë‹ˆë‹¤
      strokeStyle: 'solid', // ì„ ì˜ ìŠ¤íƒ€ì¼ ì…ë‹ˆë‹¤
      fillColor: '#CFE7FF', // ì±„ìš°ê¸° ìƒ‰ê¹”ì…ë‹ˆë‹¤
      fillOpacity: 0.6, // ì±„ìš°ê¸° ë¶ˆíˆ¬ëª…ë„ ì…ë‹ˆë‹¤
    });

    // ì§€ë„ì— ì›ì„ í‘œì‹œí•©ë‹ˆë‹¤
    circle.setMap(map);
  };

  const colorRed = '#eb4d4b';
  const colorOrg = '#f39c12';
  const colorGrn = '#27ae60';
  //patient circles
  const makeMarkerInfected = (_patient : any, color:string) => {
    const circle = new window.kakao.maps.Circle({
      center: new window.kakao.maps.LatLng(_patient.lat, _patient.lng), // ì›ì˜ ì¤‘ì‹¬ì¢Œí‘œ ì…ë‹ˆë‹¤
      radius: 1200, // ë¯¸í„° ë‹¨ìœ„ì˜ ì›ì˜ ë°˜ì§€ë¦„ì…ë‹ˆë‹¤
      strokeWeight: 1, // ì„ ì˜ ë‘ê»˜ì…ë‹ˆë‹¤
      strokeColor: `${color}`, // ì„ ì˜ ìƒ‰ê¹”ì…ë‹ˆë‹¤
      strokeOpacity: 0.7, // ì„ ì˜ ë¶ˆíˆ¬ëª…ë„ ì…ë‹ˆë‹¤ 1ì—ì„œ 0 ì‚¬ì´ì˜ ê°’ì´ë©° 0ì— ê°€ê¹Œìš¸ìˆ˜ë¡ íˆ¬ëª…í•©ë‹ˆë‹¤
      strokeStyle: 'solid', // ì„ ì˜ ìŠ¤íƒ€ì¼ ì…ë‹ˆë‹¤
      fillColor: `${color}`, // ì±„ìš°ê¸° ìƒ‰ê¹”ì…ë‹ˆë‹¤
      fillOpacity: 0.7, // ì±„ìš°ê¸° ë¶ˆíˆ¬ëª…ë„ ì…ë‹ˆë‹¤
    });
    
    // ì§€ë„ì— ì›ì„ í‘œì‹œí•©ë‹ˆë‹¤
    circle.setMap(map);
  };

  const btn_reload = () => {
    const loadedCoords = localStorage.getItem('coords');
    if(loadedCoords === null) {
      window.location.reload();
    } else {
      const parsedCoords = JSON.parse(loadedCoords);
      setLatitude(parsedCoords.lat);
      setLongitude(parsedCoords.lng);
      setCountInCircle(0);
      window.kakao.maps.load(() => {
        DeleteMapElements();
        let container = document.getElementById('map');
        let options = {
          center: new window.kakao.maps.LatLng(
            latitude,
            longitude
          ),
          level: 8,
        };
        map = new window.kakao.maps.Map(container, options);
        let geocoder = new window.kakao.maps.services.Geocoder();

        const searchAddrFromCoords = (coords : any, callback : any) : void=> {
          // ì¢Œí‘œë¡œ í–‰ì •ë™ ì£¼ì†Œ ì •ë³´ë¥¼ ìš”ì²­í•©ë‹ˆë‹¤
          geocoder.coord2RegionCode(coords.getLng(), coords.getLat(), callback);         
        }
        const displayCenterInfo = (result : any, status : any) => {
          if (status === window.kakao.maps.services.Status.OK) {
            var infoDiv : any = document.getElementById('centerAddr');
            for(var i = 0; i < result.length; i++) {
              // í–‰ì •ë™ì˜ region_type ê°’ì€ 'H' ì´ë¯€ë¡œ
              if (result[i].region_type === 'H') {
                infoDiv.innerHTML = result[i].address_name;
                break;
              }
            }
          }    
        }
        // í˜„ì¬ ì§€ë„ ì¤‘ì‹¬ì¢Œí‘œë¡œ ì£¼ì†Œë¥¼ ê²€ìƒ‰í•´ì„œ ìƒë‹¨ì— í‘œì‹œí•©ë‹ˆë‹¤
        searchAddrFromCoords(map.getCenter(), displayCenterInfo);

      
      });
      makeMarkerMyPos(latitude, longitude);

      makeArrayPatient();
    }
    init();
  }

  const btn_search = () => {
    const form : any= document.querySelector('.form-search');
    const menu_wrap = document.querySelector('#menu_wrap');
    if(formState === 'none'){
      form.classList.remove('none');
      form.classList.add('show');
      setFormState('show');
      menu_wrap?.classList.remove('show');
      menu_wrap?.classList.add('none');
    } else {
      form.classList.remove('show');
      form.classList.add('none');
      setFormState('none');
      menu_wrap?.classList.remove('show');
      menu_wrap?.classList.add('none');
    }
  }

  const onChangeSearch = (e : any) =>{
    setSearch(e.target.value);
  }

  const onSubmitForm = (e : any) => {
    const menu_wrap = document.querySelector('#menu_wrap');
    menu_wrap?.classList.toggle('none');
    menu_wrap?.classList.toggle('show');
    e.preventDefault();
    DeleteMapElements();
    window.kakao.maps.load(() => {
      DeleteMapElements();
      let container = document.getElementById('map');
      let options = {
        center: new window.kakao.maps.LatLng(
          latitude,
          longitude
        ),
        level: 8,
      };
      map = new window.kakao.maps.Map(container, options);
      // let geocoder = new window.kakao.maps.services.Geocoder();

      // geocoder.addressSearch(search, function(result : any, status : any) {

      //   // ì •ìƒì ìœ¼ë¡œ ê²€ìƒ‰ì´ ì™„ë£Œëìœ¼ë©´ 
      //   if (status === window.kakao.maps.services.Status.OK) {
      //     var coords = new window.kakao.maps.LatLng(result[0].y, result[0].x);

      //     // ê²°ê³¼ê°’ìœ¼ë¡œ ë°›ì€ ìœ„ì¹˜ë¥¼ ë§ˆì»¤ë¡œ í‘œì‹œí•©ë‹ˆë‹¤
      //     var marker = new window.kakao.maps.Marker({
      //       map: map,
      //       position: coords
      //     });

      //     map.setCenter(coords);
      //     makeMarkerMyPos(result[0].y, result[0].x);
      //   } 
      // });

      // ë§ˆì»¤ë¥¼ ë‹´ì„ ë°°ì—´ì…ë‹ˆë‹¤
      var markers : any= [];

      // ì¥ì†Œ ê²€ìƒ‰ ê°ì²´ë¥¼ ìƒì„±í•©ë‹ˆë‹¤
      var ps = new window.kakao.maps.services.Places();  

      // ê²€ìƒ‰ ê²°ê³¼ ëª©ë¡ì´ë‚˜ ë§ˆì»¤ë¥¼ í´ë¦­í–ˆì„ ë•Œ ì¥ì†Œëª…ì„ í‘œì¶œí•  ì¸í¬ìœˆë„ìš°ë¥¼ ìƒì„±í•©ë‹ˆë‹¤
      var infowindow = new window.kakao.maps.InfoWindow({zIndex:1});

      // ê²€ìƒ‰ê²°ê³¼ ëª©ë¡ ë˜ëŠ” ë§ˆì»¤ë¥¼ í´ë¦­í–ˆì„ ë•Œ í˜¸ì¶œë˜ëŠ” í•¨ìˆ˜ì…ë‹ˆë‹¤
      // ì¸í¬ìœˆë„ìš°ì— ì¥ì†Œëª…ì„ í‘œì‹œí•©ë‹ˆë‹¤
      const displayInfowindow = (marker : any , title : any) => {
        var content = '<div style="padding:5px;z-index:1;">' + title + '</div>';

        infowindow.setContent(content);
        infowindow.open(map, marker);
      }

      // ê²€ìƒ‰ê²°ê³¼ ëª©ë¡ì˜ ìì‹ Elementë¥¼ ì œê±°í•˜ëŠ” í•¨ìˆ˜ì…ë‹ˆë‹¤
      const removeAllChildNods = (el : any) => {   
        while (el.hasChildNodes()) {
            el.removeChild (el.lastChild);
        }
      }

      // ê²€ìƒ‰ê²°ê³¼ ëª©ë¡ í•˜ë‹¨ì— í˜ì´ì§€ë²ˆí˜¸ë¥¼ í‘œì‹œëŠ” í•¨ìˆ˜ì…ë‹ˆë‹¤
      const displayPagination = (pagination: any) => {
        var paginationEl : any = document.getElementById('pagination'),
            fragment : any = document.createDocumentFragment(),
            i; 

        // ê¸°ì¡´ì— ì¶”ê°€ëœ í˜ì´ì§€ë²ˆí˜¸ë¥¼ ì‚­ì œí•©ë‹ˆë‹¤
        while (paginationEl.hasChildNodes()) {
            paginationEl.removeChild (paginationEl.lastChild);
        }

        for (i=1; i<=pagination.last; i++) {
            var el = document.createElement('a');
            el.href = "#";
            el.innerHTML = `${i}`;

            if (i===pagination.current) {
                el.className = 'on';
            } else {
                el.onclick = (function(i) {
                    return function() {
                        pagination.gotoPage(i);
                    }
                })(i);
            }

            fragment.appendChild(el);
        }
        paginationEl.appendChild(fragment);
      }

      // ì§€ë„ ìœ„ì— í‘œì‹œë˜ê³  ìˆëŠ” ë§ˆì»¤ë¥¼ ëª¨ë‘ ì œê±°í•©ë‹ˆë‹¤
      const removeMarker = () => {
        for ( var i = 0; i < markers.length; i++ ) {
            markers[i].setMap(null);
        }   
        markers = [];
      }

      // ë§ˆì»¤ë¥¼ ìƒì„±í•˜ê³  ì§€ë„ ìœ„ì— ë§ˆì»¤ë¥¼ í‘œì‹œí•˜ëŠ” í•¨ìˆ˜ì…ë‹ˆë‹¤
      const addMarker = (position : any, idx : any, title? : any) => {
        var imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png', // ë§ˆì»¤ ì´ë¯¸ì§€ url, ìŠ¤í”„ë¼ì´íŠ¸ ì´ë¯¸ì§€ë¥¼ ì”ë‹ˆë‹¤
            imageSize = new window.kakao.maps.Size(36, 37),  // ë§ˆì»¤ ì´ë¯¸ì§€ì˜ í¬ê¸°
            imgOptions =  {
                spriteSize : new window.kakao.maps.Size(36, 691), // ìŠ¤í”„ë¼ì´íŠ¸ ì´ë¯¸ì§€ì˜ í¬ê¸°
                spriteOrigin : new window.kakao.maps.Point(0, (idx*46)+10), // ìŠ¤í”„ë¼ì´íŠ¸ ì´ë¯¸ì§€ ì¤‘ ì‚¬ìš©í•  ì˜ì—­ì˜ ì¢Œìƒë‹¨ ì¢Œí‘œ
                offset: new window.kakao.maps.Point(13, 37) // ë§ˆì»¤ ì¢Œí‘œì— ì¼ì¹˜ì‹œí‚¬ ì´ë¯¸ì§€ ë‚´ì—ì„œì˜ ì¢Œí‘œ
            },
            markerImage = new window.kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
                marker = new window.kakao.maps.Marker({
                position: position, // ë§ˆì»¤ì˜ ìœ„ì¹˜
                image: markerImage 
            });

        marker.setMap(map); // ì§€ë„ ìœ„ì— ë§ˆì»¤ë¥¼ í‘œì¶œí•©ë‹ˆë‹¤
        markers.push(marker);  // ë°°ì—´ì— ìƒì„±ëœ ë§ˆì»¤ë¥¼ ì¶”ê°€í•©ë‹ˆë‹¤

        return marker;
      }

      // ê²€ìƒ‰ê²°ê³¼ í•­ëª©ì„ Elementë¡œ ë°˜í™˜í•˜ëŠ” í•¨ìˆ˜ì…ë‹ˆë‹¤
      const getListItem = (index : any, places : any) => {

        var el : any = document.createElement('li'),
        itemStr = '<span class="markerbg marker_' + (index+1) + '"></span>' +
                    '<div class="info">' +
                    '   <h5>' + places.place_name + '</h5>';

        if (places.road_address_name) {
            itemStr += '    <span>' + places.road_address_name + '</span>' +
                        '   <span class="jibun gray">' +  places.address_name  + '</span>';
        } else {
            itemStr += '    <span class="make-address">' +  places.address_name  + '</span>'; 
        }
                    
          itemStr += '  <span class="tel">'+ `<a href="tel:${places.phone}">${places.phone}</a>` + '</span>' +
                    '</div>';           

        el.innerHTML = itemStr;
        el.className = 'item';

        return el;
      }

      // ê²€ìƒ‰ ê²°ê³¼ ëª©ë¡ê³¼ ë§ˆì»¤ë¥¼ í‘œì¶œí•˜ëŠ” í•¨ìˆ˜ì…ë‹ˆë‹¤
      const displayPlaces = (places : any) => {

        var listEl = document.getElementById('placesList'), 
        menuEl : any = document.getElementById('menu_wrap'),
        fragment = document.createDocumentFragment(), 
        bounds = new window.kakao.maps.LatLngBounds(), 
        listStr = '';
        
        // ê²€ìƒ‰ ê²°ê³¼ ëª©ë¡ì— ì¶”ê°€ëœ í•­ëª©ë“¤ì„ ì œê±°í•©ë‹ˆë‹¤
        removeAllChildNods(listEl);

        // ì§€ë„ì— í‘œì‹œë˜ê³  ìˆëŠ” ë§ˆì»¤ë¥¼ ì œê±°í•©ë‹ˆë‹¤
        removeMarker();
        
        for ( var i=0; i<places.length; i++ ) {

            // ë§ˆì»¤ë¥¼ ìƒì„±í•˜ê³  ì§€ë„ì— í‘œì‹œí•©ë‹ˆë‹¤
            var placePosition = new window.kakao.maps.LatLng(places[i].y, places[i].x),
                marker : any = addMarker(placePosition, i), 
                itemEl : any = getListItem(i, places[i]); // ê²€ìƒ‰ ê²°ê³¼ í•­ëª© Elementë¥¼ ìƒì„±í•©ë‹ˆë‹¤

            // ê²€ìƒ‰ëœ ì¥ì†Œ ìœ„ì¹˜ë¥¼ ê¸°ì¤€ìœ¼ë¡œ ì§€ë„ ë²”ìœ„ë¥¼ ì¬ì„¤ì •í•˜ê¸°ìœ„í•´
            // LatLngBounds ê°ì²´ì— ì¢Œí‘œë¥¼ ì¶”ê°€í•©ë‹ˆë‹¤
            bounds.extend(placePosition);

            // ë§ˆì»¤ì™€ ê²€ìƒ‰ê²°ê³¼ í•­ëª©ì— mouseover í–ˆì„ë•Œ
            // í•´ë‹¹ ì¥ì†Œì— ì¸í¬ìœˆë„ìš°ì— ì¥ì†Œëª…ì„ í‘œì‹œí•©ë‹ˆë‹¤
            // mouseout í–ˆì„ ë•ŒëŠ” ì¸í¬ìœˆë„ìš°ë¥¼ ë‹«ìŠµë‹ˆë‹¤
            (function(marker, title) {
                window.kakao.maps.event.addListener(marker, 'mouseover', function() {
                    displayInfowindow(marker, title);
                });

                window.kakao.maps.event.addListener(marker, 'mouseout', function() {
                    infowindow.close();
                });

                itemEl.onmouseover =  function () {
                    displayInfowindow(marker, title);
                };

                itemEl.onmouseout =  function () {
                    infowindow.close();
                };
            })(marker, places[i].place_name);

            fragment.appendChild(itemEl);
        }

        // ê²€ìƒ‰ê²°ê³¼ í•­ëª©ë“¤ì„ ê²€ìƒ‰ê²°ê³¼ ëª©ë¡ Elemnetì— ì¶”ê°€í•©ë‹ˆë‹¤
        listEl?.appendChild(fragment);
        menuEl.scrollTop = 0;

        // ê²€ìƒ‰ëœ ì¥ì†Œ ìœ„ì¹˜ë¥¼ ê¸°ì¤€ìœ¼ë¡œ ì§€ë„ ë²”ìœ„ë¥¼ ì¬ì„¤ì •í•©ë‹ˆë‹¤
        map.setBounds(bounds);
      }


      // ì¥ì†Œê²€ìƒ‰ì´ ì™„ë£Œëì„ ë•Œ í˜¸ì¶œë˜ëŠ” ì½œë°±í•¨ìˆ˜ ì…ë‹ˆë‹¤
      const placesSearchCB = (data : any, status : any, pagination : any) => {
        if (status === window.kakao.maps.services.Status.OK) {

            // ì •ìƒì ìœ¼ë¡œ ê²€ìƒ‰ì´ ì™„ë£Œëìœ¼ë©´
            // ê²€ìƒ‰ ëª©ë¡ê³¼ ë§ˆì»¤ë¥¼ í‘œì¶œí•©ë‹ˆë‹¤
            displayPlaces(data);

            // í˜ì´ì§€ ë²ˆí˜¸ë¥¼ í‘œì¶œí•©ë‹ˆë‹¤
            displayPagination(pagination);

        } else if (status === window.kakao.maps.services.Status.ZERO_RESULT) {

            alert('ê²€ìƒ‰ ê²°ê³¼ê°€ ì¡´ì¬í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤.');
            return;

        } else if (status === window.kakao.maps.services.Status.ERROR) {

            alert('ê²€ìƒ‰ ê²°ê³¼ ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤.');
            return;

        }
      } 

      // í‚¤ì›Œë“œ ê²€ìƒ‰ì„ ìš”ì²­í•˜ëŠ” í•¨ìˆ˜ì…ë‹ˆë‹¤
      const searchPlaces = () => {

        var key : any = document.getElementById('keyword');
        let keyword : any = key.value;

        if (!keyword.replace(/^\s+|\s+$/g, '')) {
            alert('í‚¤ì›Œë“œë¥¼ ì…ë ¥í•´ì£¼ì„¸ìš”!');
            return false;
        }

        // ì¥ì†Œê²€ìƒ‰ ê°ì²´ë¥¼ í†µí•´ í‚¤ì›Œë“œë¡œ ì¥ì†Œê²€ìƒ‰ì„ ìš”ì²­í•©ë‹ˆë‹¤
        ps.keywordSearch( keyword, placesSearchCB); 
      }

      // í‚¤ì›Œë“œë¡œ ì¥ì†Œë¥¼ ê²€ìƒ‰í•©ë‹ˆë‹¤
      searchPlaces();

      
    
    });
    setCountInCircle(0);
    makeArrayPatient();
    const form : any= document.querySelector('.form-search');
    form.classList.remove('show');
    form.classList.add('none');
    setFormState('none');
  }
  
  const DeleteMapElements = () => {
    let deleteMap : any = document.getElementById("map");
    while ( deleteMap.hasChildNodes() ) { deleteMap.removeChild( deleteMap.firstChild ); }
  }

  const init = () => {
    const Container : any = document.getElementById('dataContainer');
    if(countInCircle < 1){
      setStateAlami({
        conditionState : 'good',
        conditionTxt : 'ì¢‹ìŒ',
        conditionFace : 'icon-smile',
        conditionBgColor : '#1289A7'
      });
      Container.style.backgroundColor = stateAlami.conditionBgColor;
    } else if ( 1 <= countInCircle && countInCircle <=2 ){
      setStateAlami({
        conditionState : 'soso',
        conditionTxt : 'ì¡°ê¸ˆ ìœ„í—˜',
        conditionFace : 'icon-meh',
        conditionBgColor : '#009432'
      });
      Container.style.backgroundColor = stateAlami.conditionBgColor;
    } else if ( 3 <= countInCircle && countInCircle <= 5 ){
      setStateAlami({
        conditionState : 'bad',
        conditionTxt : 'ìœ„í—˜',
        conditionFace : 'icon-frown',
        conditionBgColor : '#cc8e35'
      });
      Container.style.backgroundColor = stateAlami.conditionBgColor;
    } else if ( 6<= countInCircle ) {
      setStateAlami({
        conditionState : 'terr',
        conditionTxt : 'ë§¤ìš° ìœ„í—˜',
        conditionFace : 'icon-emo-devil',
        conditionBgColor : '#b33939'
      });
      Container.style.backgroundColor = stateAlami.conditionBgColor;
    }
  }

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(onEvent, onError);
    
    //this.watchId = navigator.geolocation.watchPosition(this.onEvent, this.onError);
    const script = document.createElement('script');
    script.async = true;
    script.src =
      `https://dapi.kakao.com/v2/maps/sdk.js?appkey=${API_KEY}`;
    document.head.appendChild(script);
    
    
    script.onload = () => {
      window.kakao.maps.load(() => {
        DeleteMapElements();
        let container : any= document.getElementById('map');

        let options = {
          // center: new window.kakao.maps.LatLng(37.506502, 127.053617),
          center: new window.kakao.maps.LatLng(
            latitude,
            longitude
          ),
          level: 8,
        };
        map = new window.kakao.maps.Map(container, options);
      });

      const saveCoords = (obj: any) => {
        localStorage.setItem('coords', JSON.stringify(obj));
      };

      const coordObj = {
        lat: latitude,
        lng: longitude,
      };
      if (latitude !== 37.4882) saveCoords(coordObj);

      makeMarkerMyPos(latitude,longitude);
    };
    init();
    return(() =>{
      DeleteMapElements()
    });
  }, [latitude, longitude]);

  const btn_close = () => {
    const menu_wrap = document.querySelector('#menu_wrap');
    menu_wrap?.classList.toggle('none');
    menu_wrap?.classList.toggle('show');
  }

  return (
    <>
      <div id="map"></div>
      <div id="menu_wrap" className="bg_white none">
        <div className="option">
          <div onClick={btn_close} className="btn-search-close"><i className="icon-cancel"></i>
            <form onSubmit={btn_search} className="kakao-search-form">
                í‚¤ì›Œë“œ : <input type="text" value={search} id="keyword" size={15} /> 
                <button type="submit">ê²€ìƒ‰í•˜ê¸°</button> 
            </form>
          </div>
        </div>
        <ul id="placesList"></ul>
        <div id="pagination"></div>
      </div>
      <div className="options">
        <Data lat={latitude} 
              lng={longitude}
              patientNum={countInCircle}
              alami={stateAlami}
        />
        <ul className="mapNav">
          {/* <li className="mapNav-list-title">í™•ì§„ì ë°œìƒ ì¶”ì´</li> */}
          <li className="navGrn">
            <i className="icon-circle icon-circle-green"></i> 5~9 ì¼ ì‚¬ì´
          </li>
          <li className="navOrg">
            <i className="icon-circle icon-circle-orange"></i> 2~4 ì¼ ì‚¬ì´
          </li>
          <li className="navRed">
            <i className="icon-circle icon-circle-red"></i> 1ì¼ ì´ë‚´
          </li>
        </ul>
        <form className="form-search none" onSubmit={onSubmitForm}>
          <input type="text" value={search} onChange={onChangeSearch}/>
        </form>
        <a href="#" className="btn" id="btn-search" onClick={btn_search}><i className="icon-search"></i></a>
        <a href="#" className="btn" id="btn-reload" onClick={btn_reload}><i className="icon-location"></i></a>
      </div>
      <NavBottom />
      {/* <InfectedMarker /> */}
    </>
  );
}
